.Comments {
    position: relative;
    left: 0.5rem;
    top: 4rem;
    width: 104%;
    min-height: 12rem;
    margin-top: 0;
    border: none;
    border-radius: 1rem;
    padding: 0.8rem 2.5rem;
    background: linear-gradient(to right, #d6d6d6, #ffffff);
    &__title {
        font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
        font-weight: 400;
        font-size: 3rem;
        margin-bottom: 2rem;
        padding: 0.2rem;
    }
}