.Card {
    margin-right: 4.5rem;
    margin-top: 2rem;
    min-height: 50rem;
    border: 0.2rem solid #404040;
    border-radius: 0.5rem;
    position: relative;
    left: 2rem;
    -webkit-box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.75);
    -moz-box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.75);
    box-shadow: 0px 0px 14px 0px rgba(0, 0, 0, 0.75);
    animation: animationCard 1s ease-in-out;
    &__image {
        width: 40rem;
        height: 35rem;
    }
    &__title {
        position: absolute;
        bottom: 10rem;
        left: 1rem;
        font-weight: bold;
        font-size: 2.5rem;
        font-family: Brush Script MT, 'cursive';
    }
    &__button {
        position: absolute;
        left: 1rem;
        text-decoration: none;
        bottom: 4rem;
        width: 8rem;
        font-size: 1.6rem;
        text-align: center;
        padding: 1rem;
        border: 1px solid black;
        border-radius: 0.3rem;
        background-color: cadetblue;
        color: white;
        &:hover {
            cursor: pointer;
            color: white;
            animation: hoverButton 0.7s ease-out;
        }
    }
    &__likes {
        font-size: 1.6rem;
        position: absolute;
        left: 29rem;
        bottom: 3.5rem;
        padding: 1.5rem;
        border: none;
        border-radius: 0.7rem;
    }
    &__comments {
        font-size: 1.6rem;
        position: absolute;
        left: 17rem;
        bottom: 5rem;
    }
    &__price {
        font-size: 1.8rem;
        font-weight: 500;
        position: absolute;
        left: 30rem;
        bottom: 10.5rem;
    }
}

@keyframes animationCard {
    0% {
        opacity: 0;
        transform: translateY(-400px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}

@keyframes hoverButton {
    0% {
        opacity: 0;
        transform: rotate(180deg)
    }
    100% {
        opacity: 1;
        transform: rotate(0);
    }
}