.Item {
    margin: 5rem;
    display: flex;
    justify-content: flex-start;
    flex-wrap: nowrap;
    border: 1px solid lightgray;
    border-radius: 15px;
    width: 90%;
    height: 350px;
    position: relative;
    background: white;
    &__image {
        width: 350px;
        height: 100%;
        margin-right: 30px;
    }
    &__details {
        &__title {
            font-weight: bold;
            font-size: 3.5rem;
            padding-top: 1rem;
        }
        &__description {
            margin: 1.5rem auto;
            letter-spacing: 0.5px;
            font-size: 1.5rem;
        }
        &__price {
            position: absolute;
            font-size: 2.5rem;
            font-weight: bold;
            bottom: 1.7rem;
        }
    }
    &__actions {
        display: flex;
        justify-content: flex-end;
        &__quantity {
            display: flex;
            justify-content: flex-start;
            align-items: center;
            margin-left: 100px;
            position: absolute;
            left: 87rem;
            bottom: 2.5rem;
            &__manipulate {
                border: none;
                font-size: 2.5rem;
                margin-top: 10rem;
                cursor: pointer;
            }
            label {
                width: 40px;
                height: 2.4rem;
                border-radius: 10px;
                text-align: center;
                font-size: 18px;
                margin-top: 100px;
                margin-left: 0;
            }
        }
        &__btn {
            position: absolute;
            bottom: 25px;
            left: 1100px;
            padding: 5px 20px;
            cursor: pointer;
            font-size: 15px;
            display: inline-block;
        }
    }
}

@media all and (max-width: 820px) {
    .Item {
        flex-direction: column;
        height: 60rem;
        width: 80%;
        &__image {
            width: 100%;
            height: 40%;
        }
        &__details {
            &__title {
                font-size: 2rem;
                padding-left: 1rem;
            }
            &__description {
                font-size: 1.2rem;
                letter-spacing: 0;
                padding-left: 1rem;
            }
            &__price {
                font-size: 2rem;
                padding-left: 1.4rem;
                margin-bottom: 0.5rem;
            }
        }
        &__actions {
            display: flex;
            justify-content: flex-end;
            &__quantity {
                display: flex;
                justify-content: flex-start;
                align-items: center;
                margin-left: 100px;
                position: absolute;
                left: 0;
                bottom: 2rem;
                &__manipulate {
                    border: none;
                    font-size: 2.2rem;
                    margin-top: 10rem;
                    cursor: pointer;
                }
                label {
                    width: 40px;
                    height: 2.4rem;
                    border-radius: 10px;
                    text-align: center;
                    font-size: 1.6rem;
                    margin-top: 100px;
                    margin-left: 0;
                }
            }
            &__btn {
                position: absolute;
                bottom: 2rem;
                left: 23rem;
                padding: 0.3rem 1.8rem;
                cursor: pointer;
                font-size: 15px;
            }
        }
    }
}
