.Overlay {
    display: block;
    background-color: rgba(19, 20, 27, 0.72);
    backdrop-filter: blur(5px);
    position: fixed;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 100;
    &__modal {
        background: rgba(19, 20, 27, 0.72);
        color: white;
        width: 700px;
        height: 250px;
        text-align: center;
        margin: auto;
        margin-top: 15vh;
        padding: 45px;
        &__message {
            margin-bottom: 4.5rem;
            font-size: 2.5rem;
        }
        &__action {
            display: inline-block;
            background-color: #acd0c0;
            padding: 8px 16px;
            text-decoration: none;
            color: black;
            font-size: 2rem;
            margin-right: 1.5rem;
            &:hover {
                background-color: #d9b44a;
                color: white;
                cursor: pointer;
            }
        }
    }
}

@media all and (max-width: 820px) {
    .Overlay {
        z-index: 0;
        &__modal {
            width: 38rem;
            height: 25rem;
            &__message {
                font-size: 1.5rem;
            }
            &__action {
                font-size: 1.5rem;
            }
        }
    }
}