.Auth {
    margin: 0;
    padding: 0;
    outline: none;
    box-sizing: border-box;
    font-family: 'Raleway', sans-serif;
    background: linear-gradient(to right, #d6d6d6, #ffffff);
    perspective: 600px;
    height: 620px;
    animation: formAnimation 0.8s ease-out;
    &__cont {
        position: relative;
        width: 35%;
        height: 580px;
        margin: 50px auto;
        transition: all 0.4s linear;
        &__main {
            width: 100%;
            height: 100%;
            text-align: center;
            background-repeat: no-repeat;
            background-size: 100% 100%;
            &__create {
                position: relative;
                width: 100%;
                height: 100%;
                padding: 18px 15px;
                background: rgba(19, 20, 27, 0.72);
                &__title {
                    text-align: center;
                    color: #f7f3f2;
                    font-weight: 400;
                    font-size: 25px;
                    margin-top: 20px;
                    margin-bottom: 15%;
                }
                &__user,
                &__email,
                &__pass {
                    width: 85%;
                    height: 45px;
                    border: none;
                    color: white;
                    margin: auto;
                    margin-top: 15px;
                    padding-left: 10px;
                    font-size: 20px;
                    font-weight: 500;
                    border-radius: 25px;
                    margin-bottom: 0;
                    background: rgba(158, 158, 158, 0.49);
                    &::placeholder {
                        color: #fff;
                        font-size: 16px;
                        font-weight: 100;
                        transition: all 0.4s ease;
                    }
                    &:focus::placeholder {
                        opacity: 0;
                    }
                }
                &__btn {
                    color: #fff;
                    cursor: pointer;
                    width: 85%;
                    height: 50px;
                    border: none;
                    font-size: 18px;
                    font-weight: 500;
                    margin-top: 20%;
                    background: #67c5e7;
                    border-radius: 25px;
                    transition: background 0.4s ease;
                    &:hover {
                        background: #2196F3;
                    }
                }
            }
        }
    }
}

@media all and (max-width: 780px) {
    .Auth {
        height: 620px;
        &__cont {
            position: relative;
            width: 80%;
            height: 55rem;
            &__main {
                width: 100%;
                height: 100%;
                text-align: center;
                background-repeat: no-repeat;
                background-size: 100% 100%;
                &__create {
                    position: relative;
                    width: 100%;
                    height: 100%;
                    padding: 18px 15px;
                    background: rgba(19, 20, 27, 0.72);
                    &__title {
                        font-size: 2.5rem;
                    }
                    &__user,
                    &__email,
                    &__pass {
                        width: 85%;
                        height: 3.5rem;
                    }
                    &__btn {
                        width: 50%;
                        height: 4rem;
                        font-size: 1.5rem;
                    }
                }
            }
        }
    }
}

@keyframes formAnimation {
    0% {
        opacity: 0;
        transform: translateY(-200px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}