.Main {
    width: 50%;
    margin: 5rem auto;
    &__hasSearch {
        padding-left: 2rem;
        width: 100%;
        height: 30px;
        font-size: 15px;
        &__field {
            margin: 35px auto;
            text-align: center;
            font-size: 2rem;
            text-transform: uppercase;
            //padding-left: 20px;
        }
        &__feedback {
            position: absolute;
            z-index: 2;
            display: block;
            width: 2.375rem;
            height: 2.375rem;
            line-height: 2.375rem;
            text-align: center;
            pointer-events: none;
            color: #aaa;
        }
        &__formControl {
            padding-left: 2rem;
            width: 100%;
            height: 30px;
            font-size: 15px;
        }
    }
    &__flex {
        display: flex;
        flex-direction: row-reverse;
        flex-wrap: wrap;
        margin: auto;
        justify-content: center;
        padding: 100px;
        &__notFound {
            font-weight: 450;
            margin: 100px auto;
            font-size: 2.5rem;
            text-align: center;
        }
    }
}