.User {
    &__btn {
        margin: 3rem 0.5rem 0 1rem;
        cursor: pointer;
    }
    &__like {
        border-radius: 6px;
        padding: 0.8rem 1.4rem;
        font-size: 2rem;
        position: relative;
        left: 0;
        bottom: -8px;
        background-color: cadetblue;
        border: none;
    }
    &__add {
        margin-left: 1.5rem;
        padding: 1rem 1.6rem;
        width: 100px;
        border-radius: 10px;
        font-weight: bold;
        cursor: pointer;
        border: none;
        background-color: black;
        color: white;
        position: relative;
        left: 0;
        bottom: -5px;
    }
}