.Cart {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    background: rgba(19, 20, 27, 0.72);
    &__summary {
        display: flex;
        align-items: flex-end;
        margin: 30px auto;
        border: 2px solid lightgray;
        min-width: 40rem;
        min-height: 250px;
        position: relative;
        background: white;
        &__title {
            position: absolute;
            top: 1rem;
            left: 2rem;
            font-size: 25px;
            font-weight: bold;
        }
        &__details {
            position: absolute;
            top: 10rem;
            left: 40px;
            display: flex;
            flex-direction: row;
            &__items {
                font-size: 1.8rem;
            }
            &__price {
                font-weight: bold;
                padding-left: 100px;
                padding-bottom: 15px;
                font-size: 2rem;
            }
        }
        &__btn {
            width: 380px;
            height: 40px;
            position: absolute;
            left: 10px;
            bottom: 10px;
            border-radius: 10px;
            background-color: darkviolet;
            color: white;
            font-size: 20px;
            border: none;
            &:hover {
                cursor: pointer;
                opacity: 0.8;
            }
        }
    }
}