.Nav {
    //padding-top: 3.5rem;
    display: flex;
    align-items: center;
    &__list {
        list-style: none;
        display: flex;
        margin: 0;
        &__item {
            margin-right: 2rem;
            color: white;
            font-size: 1.6rem;
            a {
                text-decoration: none;
                color: white;
                font-size: 2rem;
                text-transform: uppercase;
                &:hover {
                    //border-bottom: 5px solid black;
                    color: #80ced6
                }
            }
            &__total {
                font-size: 2rem;
            }
        }
    }
    .hamburger {
        display: none;
    }
}

@media all and (max-width: 920px) {
    .Nav {
        position: relative;
        z-index: 2;
        &__list {
            background-color: rgba(19, 20, 27, 0.72);
            padding: 1.6rem;
            list-style: none;
            display: flex;
            position: absolute;
            top: 100%;
            right: -20%;
            flex-direction: column;
            justify-content: space-between;
            margin: 0;
            &--active {
                animation-name: showNavLinks;
                animation-duration: 1.5s;
                animation-fill-mode: backwards;
            }
            &--unactive {
                opacity: 0;
            }
            &__item {
                margin-bottom: 1.8rem;
                color: white;
                a {
                    text-decoration: none;
                    color: white;
                    font-size: 1.5rem;
                    text-transform: uppercase;
                    &:hover {
                        color: #80ced6;
                    }
                }
            }
        }
        .hamburger {
            display: block;
        }
    }
}

@keyframes showNavLinks {
    0% {
        opacity: 0;
        transform: translateX(50px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}