.Container {
    position: relative;
    min-width: 100%;
    min-height: 1200px;
    background: white;
    display: flex;
    animation: containerAnimation 1s ease-out;
    &__title {
        padding: 2rem;
        margin-bottom: 0;
        font-size: 4rem;
        color: #2F4F4F;
        font-weight: 500;
        line-height: 1.6;
    }
    &__image {
        height: 60%;
        width: 55%;
        padding: 35px;
        position: absolute;
        top: 80px;
        left: 5px;
    }
    &__details {
        width: 42%;
        max-height: 250px;
        position: absolute;
        left: 750px;
        top: 120px;
        display: flex;
        flex-wrap: wrap;
        &__description {
            width: 42%;
            max-height: 250px;
            position: absolute;
            left: 210px;
            top: -80px;
            font-size: 2.8rem;
        }
        &__p {
            line-height: 1.5;
            border-top: 3px solid black;
            padding: 15px 5px;
            font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
            font-size: 20px;
            text-transform: capitalize;
            letter-spacing: 1px;
        }
    }
}

@media all and (min-width:320px) and (max-width: 920px) {
    .Container {
        display: flex;
        flex-direction: column;
        position: relative;
        min-width: 70%;
        padding: 1.5rem;
        &__image {
            height: 40%;
            width: 100%;
        }
        &__title {
            font-size: 3rem;
            margin: 2rem;
        }
        &__details {
            width: 100%;
            max-height: 250px;
            position: absolute;
            left: 20px;
            top: 650px;
            display: flex;
            flex-wrap: wrap;
            &__description {
                width: 100%;
                max-height: 250px;
                position: absolute;
                left: 0px;
                font-size: 2.5rem;
                text-align: center;
            }
            &__p {
                line-height: 1.5;
                border-top: 3px solid black;
                padding: 15px 5px;
                font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
                font-size: 1.5rem;
                text-transform: capitalize;
                letter-spacing: 0.1rem;
            }
        }
    }
}

@media all and (min-width: 1400px) and (max-width: 1900px) {
    .Container {
        &__details {
            position: absolute;
            left: 97rem;
            top: 12rem;
        }
    }
}

@keyframes containerAnimation {
    0% {
        opacity: 0;
        transform: translateX(-150px);
    }
    75% {
        opacity: 0.5;
        transform: translateX(50px);
    }
    100% {
        opacity: 1;
        transform: translateX(0);
    }
}