.CommentForm {
    &__input {
        padding: 1rem;
        margin-bottom: 1rem;
        font-size: 1.5rem;
        border: none;
        border-radius: 0.5rem;
    }
    &__submit {
        background-color: rebeccapurple;
        color: white;
        padding: 1.5rem;
        border: none;
        border-radius: 0.5rem;
        cursor: pointer;
        margin-right: 1rem;
    }
}