.Home {
    height: 48rem;
    padding: 3rem 0;
    background: rgba(19, 20, 27, 0.72);
    &__image {
        width: 100%;
    }
    &__details {
        display: flex;
        flex-direction: column;
        margin-top: 2rem;
        padding-left: 0.5rem;
        animation: animationOnForum 1.5s ease-out;
        &__title {
            font-size: 4.5rem;
            color: white;
            font-weight: 300;
            margin-bottom: 1.2rem;
        }
        &__description {
            font-size: 1.5rem;
            text-transform: uppercase;
            color: white;
            font-weight: 150;
        }
    }
}

@media all and (max-width: 800px) {
    .Home {
        height: 32rem;
        &__image {
            height: 15rem;
        }
        &__details {
            &__title {
                font-size: 3rem;
            }
            &__description {
                font-size: 1rem;
            }
        }
    }
}

@media all and (min-width: 1350px) and (max-width: 1800px) {
    .Home {
        height: 53rem;
    }
}

@keyframes animationOnForum {
    0% {
        opacity: 0;
        transform: translateY(200px);
    }
    80% {
        opacity: 0.7;
        transform: translateY(-20px);
    }
    100% {
        opacity: 1;
        transform: translateY(0);
    }
}