.CommentUser {
    width: 100%;
    height: 3.5rem;
    margin-bottom: 3rem;
    &__content {
        font-size: 1.8rem;
        font-weight: 400;
    }
    &__delete {
        position: absolute;
        right: 80px;
        font-size: 18px;
        border: none;
        background-color: white;
        cursor: pointer;
        margin-bottom: 5px;
        padding: 2px 10px;
    }
    &__name {
        font-size: 1.5rem;
    }
}

@media all and (max-width: 800px) {
    .CommentUser {
        width: 90%;
        &__content {
            font-size: 1.5rem;
        }
        &__delete {
            font-size: 1.3rem;
            position: absolute;
            right: 5rem;
        }
    }
}