.Owner {
    &__btn {
        margin: 3rem 0.5rem 0 1rem;
        cursor: pointer;
    }
    &__delete {
        background: red;
        color: white;
        border: none;
        padding: 1.2rem 1.8rem;
        font-size: 1.8rem;
        border-radius: 0.6rem;
    }
    &__edit {
        text-decoration: none;
        border: none;
        padding: 1.2rem 2.2rem;
        border-radius: 0.6rem;
        font-size: 1.8rem;
        background-color: thistle;
        color: black;
    }
}