.Create {
    margin: 0;
    padding: 0;
    height: 600px;
    width: 100%;
    position: relative;
    background: linear-gradient(to right, #d6d6d6, #ffffff);
    &__wrapper {
        position: absolute;
        left: 200px;
        top: 70px;
        height: 80%;
        border-radius: 10px;
        background-repeat: no-repeat;
        background-size: 100% 100%;
        background: rgba(19, 20, 27, 0.72);
        box-shadow: 0px 20px 50px 0px #000000;
        &__formTitle {
            text-align: center;
            margin-top: 4rem;
            color: white;
            font-size: 2.5rem;
            font-weight: 400;
        }
        &__form {
            &__title,
            &__description,
            &__image,
            &__price {
                background: rgba(19, 20, 27, 0.72);
                height: 5rem;
                width: 85%;
                color: white;
                padding: 10px;
                margin-top: 1.5rem;
                margin-left: 6.2rem;
                font-size: 15px;
                border: none;
                border-top-left-radius: 15px;
                border-top-right-radius: 15px;
                border-bottom-left-radius: 15px;
                border-bottom-right-radius: 15px;
            }
            &__description {
                height: 9rem;
            }
            &__btn {
                position: absolute;
                bottom: 30px;
                left: 6.2rem;
                padding: 14px 19px;
                font-size: 14px;
                font-weight: 500;
                transition: background 0.4s ease;
                background: #67c5e7;
                border: none;
                border-radius: 10px;
                cursor: pointer;
                &:hover {
                    background: #2196F3;
                }
            }
        }
    }
}

@media all and (min-width:350px) and (max-width: 767px) {
    .Create {
        height: 500px;
        &__wrapper {
            position: absolute;
            left: 100px;
            top: 70px;
            right: 50px;
            height: 70%;
            border-radius: 10px;
            background-repeat: no-repeat;
            background-size: 100% 100%;
            background: rgba(19, 20, 27, 0.72);
            box-shadow: 0px 20px 50px 0px #000000;
            &__formTitle {
                text-align: start;
                margin-top: 4rem;
                color: white;
                font-size: 1.5rem;
                font-weight: 400;
                margin-left: 6rem;
            }
            &__form {
                display: flex;
                &__title,
                &__description,
                &__image,
                &__price {
                    background: rgba(19, 20, 27, 0.72);
                    height: 3rem;
                    width: 70%;
                    color: red;
                    padding: 10px;
                    margin-top: 1.5rem;
                    font-size: 1.2rem;
                    border: none;
                    border-top-left-radius: 15px;
                    border-top-right-radius: 15px;
                    border-bottom-left-radius: 15px;
                    border-bottom-right-radius: 15px;
                    margin-left: 5.5rem;
                }
                &__description {
                    height: 5rem;
                }
                &__btn {
                    position: absolute;
                    bottom: 3rem;
                    left: 5.5rem;
                    padding: 0.6rem 1rem;
                    font-size: 1rem;
                    font-weight: 500;
                    transition: background 0.4s ease;
                    background: #67c5e7;
                    border: none;
                    border-radius: 10px;
                    cursor: pointer;
                    text-align: center;
                    &:hover {
                        background: #2196F3;
                    }
                }
            }
        }
    }
}

@media all and (min-width: 1400px) and (max-width: 1800px) {
    .Create {
        &__wrapper {
            position: absolute;
            left: 32rem;
        }
    }
}